<template>
  <div class="file-view">
    <head-top title="">
    </head-top>
    <r-file-view :file-id="fileInfo.id" :file-name="fileInfo.fileName" :file-ext="fileInfo.fileExt"
                 :file-total="fileTotal" @changePageIndex="changePageIndex">
    </r-file-view>
  </div>
</template>

<script>
import RFileView from "@/components/r-file-view";

export default {
  name: "fileView",
  components: { RFileView },
  data () {
    return {
      canOpenFileExt: ["txt", "bmp", "gif", "jpg", "png", "ico"],
      fileInfo: this.$store.getters.cloudFile,
      fileTotal: 1
    };
  },
  mounted () {
    this.init();
  },
  methods: {
    init () {
      if (!this.canOpenFileExt.find(r => r === this.fileInfo.fileExt)) {
        this.$mainHttp.material.fileConvertCount({
          mid: this.fileInfo.id
        }).then(res => {
          if (res.Result) {
            this.fileTotal = res.Total;
          } else if (res.Info) {
            this.$toast(res.Info);
            this.fileTotal = 0;
          } else {
            this.fileTotal = 0;
          }
        }).catch(() => {
          this.fileTotal = 0;
        });
      }
    },
    changePageIndex (index) {
      if (this.$store.getters.course) {
        this.$gatewayHttp.broadcast.turnToPageIndex({
          mid: this.fileInfo.id,
          suffix: this.fileInfo.fileExt,
          index: index
        });
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";
.file-view {
  min-height: 100%;
  background-color: #fcfcfc;
}
</style>
