<template>
  <div class="r-video">
    <!-- ; ios  controls-->
    <video
      ref="video"
      v-if="showVideo"
      webkit-playsinline="true"
      playsinline="true"
      x-webkit-airplay="true"
      x5-video-player-type="h5"
      x5-video-player-fullscreen="true"
      x5-video-orientation="landscape"
      style="object-fit:fill"
      controlslist="nodownload"
      preload="false"
      :poster="poster"
      :src="src"
      @waiting="handleWaiting"
      @canplaythrough="state.isLoading = false"
      @playing="(state.isLoading = false), (state.controlBtnShow = false), (state.playing = true)"
      @stalled="state.isLoading = true"
      @error="handleError">
      HTML5
    </video>
    <!--
  object-fit: fill   video
  poster:"img.jpg" 
  autoplay 
     auto - 
     meta - 
     none - 

  muted
  webkit-playsinline playsinline:   
  x5-video-player-type="h5" :  x5H5
  x5-video-player-fullscreen="true"  turefalse
  x5-video-orientation="portraint" landscape ,portraint
                                     portraintH5
                                     x5-video-player-typeH5
    -->
    <!-- Android,  -->
    <img v-show="!showVideo || state.isEnd" class="poster" :src="poster" alt/>
    <!--  -->
    <div class="control" v-show="!state.isError" ref="control" @touchstart="touchEnterVideo"
         @touchend="touchLeaveVideo">
      <!--  ||  || -->
      <div class="play" @touchstart.stop="clickPlayBtn" v-show="state.controlBtnShow">
        <img v-show="!state.playing && !state.isLoading" src="./../../assets/video/content_btn_play.svg" alt=""/>
        <img v-show="state.playing && !state.isLoading" src="./../../assets/video/content_btn_pause.svg" alt=""/>
        <div class="loader" v-show="state.isLoading">
          <div class="loader-inner ball-clip-rotate">
            <div></div>
          </div>
        </div>
      </div>
      <!--  -->
      <!-- <div class="control-bar" v-show="state.controlBarShow"> -->
      <div class="control-bar" :style="{ visibility: state.controlBarShow ? 'visible' : 'hidden' }">
        <span class="time">{{ video.displayTime }}</span>
        <span class="progress" ref="progress">
          <img class="progress-btn ignore" :style="{
              transform: `translate3d(${video.progress.current}px, 0, 0)`
            }" src="./../../assets/video/content_ic_tutu.svg" alt=""/>
          <span class="progress-loaded" :style="{ width: `${video.loaded}%` }"></span>
          <!--  -->
          <span class="progress-move" @touchmove.stop.prevent="moveIng($event)" @touchstart.stop="moveStart($event)"
                @touchend.stop="moveEnd($event)">
          </span>
        </span>

        <span class="total-time">{{ video.totalTime }}</span>
        <span class="full-screen" @click="fullScreen">
          <img src="./../../assets/video/content_ic_increase.svg" alt/>
        </span>
      </div>
    </div>
    <!--  -->
    <div class="error" v-show="state.isError">
      <p class="lose"></p>
      <p class="retry" @click="retry"></p>
    </div>
  </div>
</template>
<script>
import { throttle } from "@/utils/tool";

const pad = val => {
  val = Math.floor(val);
  if (val < 10) {
    return "0" + val;
  }
  return val + "";
};
const timeParse = sec => {
  let min = 0;
  min = Math.floor(sec / 60);
  sec = sec - min * 60;
  return pad(min) + ":" + pad(sec);
};

export default {
  name: "r-video",
  props: {
    src: {
      type: String,
      required: true
    },
    poster: {
      type: String,
      default: ""
    },
    showVideo: {
      // android
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      // video
      $video: null,
      // 
      player: {
        $player: null,
        pos: null
      },
      // progress
      progressBar: {
        $progress: null, // DOM
        pos: null
      },
      // video
      video: {
        loaded: 0, // 
        displayTime: "00:00", // 
        totalTime: "00:00", // 
        progress: {
          width: 0, // 
          current: 0 // 
        }
      },
      // 
      hideTimer: null,
      // 
      state: {
        controlBtnShow: true, // 
        controlBarShow: false, // 
        fullScreen: false,
        playing: false,
        isLoading: false,
        isEnd: false,
        isError: false
      },
      // 
      isFirstTouch: true,
      // 
      isPauseTouch: false
    };
  },

  methods: {
    init () {
      // video,video
      this.$video = this.$el.getElementsByTagName("video")[0];
      this.initPlayer();
    },
    // , video-player
    // getBoundingClientRect()client
    initPlayer () {
      const $player = this.$el;
      const $progress = this.$el.getElementsByClassName("progress")[0];
      // 
      this.player.$player = $player;
      this.progressBar.$progress = $progress;
      this.player.pos = $player.getBoundingClientRect();
      this.progressBar.pos = $progress.getBoundingClientRect();
      this.video.progress.width = Math.round(
        $progress.getBoundingClientRect().width
      );
    },
    //  & 
    clickPlayBtn () {
      if (this.state.isLoading) return;
      this.isFirstTouch = false;
      this.state.playing = !this.state.playing;
      this.state.isEnd = false;
      if (this.$video) {
        // 
        if (this.state.playing) {
          try {
            this.$video.play();
            this.$emit("selectChange");
            this.isPauseTouch = false;
            // 
            this.$video.addEventListener("progress", e => {
              this.getLoadTime();
            });
            // 
            this.$video.addEventListener(
              "timeupdate",
              throttle(this.getPlayTime, 100, 1)
            );
            // 
            this.$video.addEventListener("ended", e => {
              // 
              this.state.playing = false;
              this.state.isEnd = true;
              this.state.controlBtnShow = true;
              this.video.displayTime = "00:00";
              this.video.progress.current = 0;
              this.$video.currentTime = 0;
              // this.src = this.src;
            });
          } catch (e) {
            // url
          }
        } else {
          // 
          this.isPauseTouch = true;
          this.$video.pause();
        }
      }
    },

    stopPlay () {
      // this.state.isEnd = true;
      // this.isFirstTouch = true;

      this.$video.load();
      this.state.playing = false;
      this.init();
    },
    // 
    touchEnterVideo () {
      if (this.isFirstTouch) return;
      if (this.hideTimer) {
        clearTimeout(this.hideTimer);
        this.hideTimer = null;
      }
      this.state.controlBtnShow = true;
      this.state.controlBarShow = true;
    },
    // 
    touchLeaveVideo () {
      if (this.isFirstTouch) return;
      if (this.hideTimer) {
        clearTimeout(this.hideTimer);
      }
      // , 
      if (this.isPauseTouch) {
        this.state.controlBtnShow = true;
        this.state.controlBarShow = true;
      } else {
        this.hideTimer = setTimeout(() => {
          this.state.controlBarShow = false;
          // loading
          if (this.state.isLoading) {
            this.state.controlBtnShow = true;
          } else {
            this.state.controlBtnShow = false;
          }
          this.hideTimer = null;
        }, 3000);
      }
    },
    // 
    handleWaiting () {
      this.state.controlBtnShow = true;
      this.state.isLoading = true;
    },
    // 
    handleError () {
      this.state.isError = true;
    },
    // 
    retry () {
      this.state.isError = false;
      this.init();
    },
    // 
    getPlayTime () {
      const percent = this.$video.currentTime / this.$video.duration;
      this.video.progress.current = Math.round(
        this.video.progress.width * percent
      );
      // 
      this.video.totalTime = timeParse(this.$video.duration);
      this.video.displayTime = timeParse(this.$video.currentTime);
    },
    // 
    getLoadTime () {
      // console.log('...',this.$video.buffered.end(0));
      this.video.loaded =
        (this.$video.buffered.end(0) / this.$video.duration) * 100;
    },
    // 
    moveStart (e) {
    },
    moveIng (e) {
      // console.log("...");
      const currentX = e.targetTouches[0].pageX;
      let offsetX = currentX - this.progressBar.pos.left;
      // 
      if (offsetX <= 0) {
        offsetX = 0;
      }
      if (offsetX >= this.video.progress.width) {
        offsetX = this.video.progress.width;
      }
      this.video.progress.current = offsetX;

      const percent = this.video.progress.current / this.video.progress.width;
      this.$video.duration && this.setPlayTime(percent, this.$video.duration);
    },
    moveEnd (e) {
      // console.log("...");
      const currentX = e.changedTouches[0].pageX;
      const offsetX = currentX - this.progressBar.pos.left;
      this.video.progress.current = offsetX;
      // offsetX
      const percent = offsetX / this.video.progress.width;
      this.$video.duration && this.setPlayTime(percent, this.$video.duration);
    },
    // 
    setPlayTime (percent, totalTime) {
      this.$video.currentTime = Math.floor(percent * totalTime);
    },
    // 
    fullScreen () {
      console.log("...");
      if (!this.state.fullScreen) {
        this.state.fullScreen = true;
        this.$video.webkitRequestFullScreen();
      } else {
        this.state.fullScreen = false;
        document.webkitCancelFullScreen();
      }
    }
  },
  mounted () {
    setTimeout(() => {
      this.init();
    }, 20);
  },
  beforeDestroy () {
  }
};
</script>
